/**
 * Creates Hypercube.js ambiant lights.
 * @example
 * const lightambiant = new LightAmbiant(name, color);
 */
class LightAmbiant {

    /**
     * Stores the color.
     * @type {number[]}
     * @protected
     */
    $color;

    /**
     * Stores the name.
     * @type {string}
     * @private
     */
    $name;

    /**
     * Stores the type.
     * @type {string}
     * @protected
     */
    $type = 'light.ambiant';

    /**
     * Gets the color.
     * @type {number[]}
     * @public
     * @readonly
     */
    get color() {

        return this.$color;
    };

    /**
     * Gets the name.
     * @type {string}
     * @public
     * @readonly
     */
    get name() {

        return this.$name;
    }

    /**
     * Creates a new Hypercube.js ambiant light.
     * @param {string} $name The name of the light.
     * @param {number[]} $color The color of the light.
     */
    constructor($name, $color) {

        this.$name = $name;
        this.$color = [...$color];
    }
}

export {

    LightAmbiant
};

export default LightAmbiant;
