import {Material} from '../../index.js';

/**
 * Creates Hypercube.js color materials.
 * @example
 * const materialcolor = new MaterialColor(name, color);
 */
class MaterialColor extends Material {

    /**
     * Stores the color.
     * @type {number[]}
     * @protected
     */
    $color;

    /**
     * Stores the type.
     * @type {string}
     * @protected
     */
    $type = 'material.color';

    /**
     * Gets the color.
     * @type {number[]}
     * @public
     * @readonly
     */
    get color() {

        return this.$color;
    };

    /**
     * Creates a new Hypercube.js color material.
     * @param {string} $name The name of the material.
     * @param {any} [$parameters] The parameters of the material.
     */
    constructor($name, $parameters) {

        super($name, $parameters);

        this.$color = [...$parameters.color];
    }
}

export {

    MaterialColor
};

export default MaterialColor;
