/**
 * Creates Hypercube.js skyboxes.
 * @example
 * const skybox = new Skybox(name, color);
 */
class Skybox {

    /**
     * Stores the color.
     * @type {number[]}
     * @protected
     */
    $color;

    /**
     * Stores the name.
     * @type {string}
     * @private
     */
    $name;

    /**
     * Gets the color.
     * @type {number[]}
     * @public
     * @readonly
     */
    get color() {

        return this.$color;
    };

    /**
     * Gets the name.
     * @type {string}
     * @public
     * @readonly
     */
    get name() {

        return this.$name;
    }

    /**
     * Creates a new Hypercube.js skybox.
     * @param {string} $name The name of the skybox.
     * @param {number[]} $color The color of the skybox.
     */
    constructor($name, $color) {

        this.$name = $name;
        this.$color = [...$color];
    }
}

export {

    Skybox
};

export default Skybox;
