/**
 * Creates update loops.
 * @example
 * const loop = new Loop(handler);
 * loop.run();
 */
class Loop {

    /**
     * Stores the elapsed time between each frame.
     * @type {number}
     * @private
     */
    $elapsedtime;

    /**
     * Stores the handler to run with the loop.
     * @type {Function}
     * @private
     */
    $handler;

    /**
     * Stores the identifier of the last requestAnimationFrame call.
     * @type {number}
     * @private
     */
    $identifier;

    /**
     * Stores the time value of the last frame call.
     * @type {number}
     * @private
     */
    $lasttime;

    /**
     * Stores the panicking threshold (in ms).
     * @type {number}
     * @private
     */
    $panickingthreshold;

    /**
     * Stores the global scope used.
     * @type {typeof globalThis}
     * @private
     */
    $scope;

    /**
     * Creates a new update loop.
     * @param {Function} $handler The handler to run with the loop.
     * @param {typeof globalThis} $scope The global scope to use.
     */
    constructor($handler, $scope = window) {

        this.$elapsedtime = 0;
        this.$handler = $handler;
        this.$panickingthreshold = 4000;
        this.$scope = $scope;
    }

    /**
     * Destroys the loop.
     * @public
     */
    destroy() {

        if (typeof this.$identifier !== 'undefined') {

            this.$scope.cancelAnimationFrame(this.$identifier);
        }
    }

    /**
     * Runs the loop.
     * @param {number} [$framerate] The framerate to run the loop (in fps).
     * @public
     */
    run($framerate = 60) {

        const currenttime = performance.now();

        if (typeof this.$lasttime !== 'undefined') {

            this.$elapsedtime += currenttime - this.$lasttime;

            const timeframe = 1000 / $framerate;

            if (this.$elapsedtime >= timeframe) {

                this.$elapsedtime -= timeframe;

                this.$handler(timeframe);
            }

            // const panicking = this.$elapsedtime >= this.$panickingthreshold;
            // const timeframe = (panicking === true) ? this.$elapsedtime : 1000 / $framerate;

            // while (this.$elapsedtime >= timeframe) {

            //     this.$elapsedtime -= timeframe;

            //     this.$handler(timeframe, panicking);
            // }
        }

        this.$identifier = this.$scope.requestAnimationFrame(this.run.bind(this, $framerate));

        this.$lasttime = currenttime;
    }

    /**
     * Sets the panicking threshold.
     * @param {number} $panickingthreshold The panicking threshold to set (in ms).
     * @public
     */
    setpanickingthreshold($panickingthreshold) {

        this.$panickingthreshold = $panickingthreshold;
    }
}

export {

    Loop
};

export default Loop;
