    $initializeinputs() {

        this.$inputs = {

            'a': false,
            'z': false,
            'e': false,
            'q': false,
            's': false,
            'd': false,
            'w': false,
            'ArrowUp': false,
            'ArrowLeft': false,
            'ArrowRight': false,
            'ArrowDown': false,
            'Enter': false,
            ' ': false,
            'm': false,
            'KeyW': false,
            'KeyA': false,
            'KeyS': false,
            'KeyD': false,
        };
    }

