    // @TODO
    /**
     *
     * @param {HYPERCUBEJS.Node} $entity
     * @param {*} $event
     */
    oninput($entity, $event) {

        this.$translation = [0, 0, 0];

        if ($event.type === 'keydown'
        && this.$inputs.hasOwnProperty($event.code) === true) {

            // @TODO : replace par event.code
            this.$inputs[$event.code] = true;

            // console.log($event.key);
        }

        else if ($event.type === 'keyup'
        && this.$inputs.hasOwnProperty($event.code) === true) {

            // @TODO : replace par event.keyCode
            this.$inputs[$event.code] = false;
        }

        else if ($event.type === 'pointermove') {

            this.engine.deviceviewer.element.requestPointerLock();

            this.$locked = true;
        }

        else if ($event.type === 'blur') {

            this.$initializeinputs();
        }

        if ($event.type === 'pointermove'
        && this.$locked === true) {

            this.$rotation[1] += - $event.movementX / 20 / this.engine.deviceviewer.ratio();
            this.$rotationcamera[0] += - (Math.sign($event.movementY) * Math.max(Math.abs($event.movementY), 5)) / 20 / this.engine.deviceviewer.ratio();
        }

        if (this.$inputs['KeyW'] === true) {
            this.$translation[2] += -1;
            // $entity.audio.play();
        }
        if ((this.$inputs[' '] === true || this.$inputs['ArrowUp'] === true || ($event.type === 'pointerdown' && $event.buttons === 1))
        && stateplayer.getstate().jumping === false
        && stateplayer.getstate().jumpcooldown <= 0) {

            stateplayer.setstate({

                'jumping': true,
                'jump': 2,
                'jumpcooldown': 1000
            });
        }
        if (this.$inputs['KeyA'] === true) {
            this.$translation[0] += -1;
            // $entity.audio.play();
        }
        if (this.$inputs['KeyS'] === true) {
            this.$translation[2] += 1;
            // $entity.audio.play();
        }
        if (this.$inputs['KeyD'] === true) {
            this.$translation[0] += 1;
            // $entity.audio.play();
        }
        // if (this.$inputs['Enter'] === true) {
        //     stategame.setstate({

        //         ...stategame.getstate(),
        //         'mountain': true,
        //         'temple': true,
        //         'tree': true,
        //         // 'sky': true
        //     });
        // }
        // if (this.$inputs['ArrowLeft'] === true) {
        //     this.$rotation[1] += 1;
        // }
        // if (this.$inputs['ArrowRight'] === true) {
        //     this.$rotation[1] += -1;
        // }
        // if (this.$inputs['m'] === true) {
        //     const ambiant = gameobjectsoundambiant();
        //     ambiant.audio.play();
        //     ambiant.audio.loop = true;
        //     ambiant.rotate([...$entity.rotation]);
        //     ambiant.translate([...$entity.translation]);
        //     ambiant.translate([0, 1, 0]);
        //     this.scene.noderoot.addchild(ambiant);
        // }
        // if (this.$inputs['z'] === false
        // && this.$inputs['q'] === false
        // && this.$inputs['s'] === false
        // && this.$inputs['d'] === false) {
        //     // $entity.audio.pause();
        // }
    }

