/**
 * Creates Hypercube.js directional lights.
 * @example
 * const lightdirectional = new LightDirectional(name, direction, color);
 */
class LightDirectional {

    /**
     * Stores the color.
     * @type {number[]}
     * @protected
     */
    $color;

    /**
     * Stores the direction.
     * @type {number[]}
     * @protected
     */
    $direction;

    /**
     * Stores the name.
     * @type {string}
     * @private
     */
    $name;

    /**
     * Stores the type.
     * @type {string}
     * @protected
     */
    $type = 'light.directional';

    /**
     * Gets the color.
     * @type {number[]}
     * @public
     * @readonly
     */
    get color() {

        return this.$color;
    };

    /**
     * Gets the direction.
     * @type {number[]}
     * @public
     * @readonly
     */
    get direction() {

        return this.$direction;
    };

    /**
     * Gets the name.
     * @type {string}
     * @public
     * @readonly
     */
    get name() {

        return this.$name;
    }

    /**
     * Creates a new Hypercube.js directional light.
     * @param {string} $name The name of the light.
     * @param {number[]} $direction The direction of the light.
     * @param {number[]} $color The color of the light.
     */
    constructor($name, $direction, $color) {

        this.$name = $name;
        this.$direction = [...$direction];
        this.$color = [...$color];
    }
}

export {

    LightDirectional
};

export default LightDirectional;
